import { createStore } from 'vuex'
import router from '../router'
import { auth } from '../firebase'
import { db } from '../firebase'
import {  collection ,addDoc ,getDocs,deleteDoc,doc} from "firebase/firestore"; 

import { 
  createUserWithEmailAndPassword,
  signInWithEmailAndPassword,
  signOut 
} from 'firebase/auth'

export default createStore({
  state: {
    user:null,
    email:"",
    em:{
      title:null,
      date:null,
      text:null

    },
data:[ ]
  },
  mutations: {
    SET_USER (state, user) {
      state.user = user
    },

    CLEAR_USER (state) {
      state.user = null
    },
    addToDo(state,em){
const{title,text,date}= em
state.em.title=String(title)
state.em.text=String(text)
state.em.date=String(date)

console.log(state.em);
    },
    docData(state,d){
     
     
       // state.data=[ ]
        state.data.push(d)
        console.log(state.data)
   

    },clearData(state){
state.data=[]
    }

  },
  actions: {
   async add (state,em){
 
  
  await addDoc(collection(db,String(em.email) ), em);
  





},async readToDo({ commit },user){
  let d
  commit("clearData"  );
  const querySnapshot = await getDocs(collection(db, user.acc));
querySnapshot.forEach((doc) => {
  d=Object.assign({id:doc.id},doc.data())
  
  // doc.data() is never undefined for query doc snapshots
  console.log(d);
  commit("docData",d );
  
});



},async delToDo(context,deli){
console.log(deli)
  
    await deleteDoc(doc(db,deli.user , deli.id));
 
  
},



    async login ({ commit }, ruser) {
      const { acc, pas } = ruser

      try {
        await signInWithEmailAndPassword(auth, acc, pas)
      } catch (error) {
        switch(error.code) {
          case 'auth/user-not-found':
            alert("User not found")
            break
          case 'auth/wrong-password':
            alert("Wrong password")
            break
          default:
            //alert("Something went wrong")
            console.log(error.code)
        }

        return
      }
      commit('email', acc)
      commit('user', auth.currentUser)
      commit('SET_USER', {acc,pas})
     router.push('/to-do-list')
    },async register ({ commit}, user) {
      const { acc, pas } = user

     try {
       await createUserWithEmailAndPassword(auth, acc, pas)
     } catch (error) {
       switch(error.code) {
         case 'auth/email-already-in-use':
           alert("Email already in use")
           break
         case 'auth/invalid-email':
           alert("Invalid email")
           break
         case 'auth/operation-not-allowed':
           alert("Operation not allowed")
           break
         case 'auth/weak-password':
           alert("Weak password")
           break
         default:
           alert("Something went wrong")
       }

       return
     }

     commit('SET_USER', auth.currentUser)
  
     router.push('/to-do-list')
   },
   async logout ({ commit }) {
    await signOut(auth)

    commit('CLEAR_USER')

    router.push('/login')
  },
  },
  modules: {
  }
})
